<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item>
                
            </el-breadcrumb-item>
            <el-breadcrumb-item>
                
            </el-breadcrumb-item>
        </el-breadcrumb>

        <el-card>

            <el-row>
                <el-col :span="8">
                    <el-input placeholder="">
                        <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
            </el-row>

            <el-table :data="orderList" style="width: 100%" border stripe>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="order_number"></el-table-column>
                <el-table-column label="" prop="order_price"></el-table-column>
                <el-table-column label="" prop="pay_status">
                    <template slot-scope="scope">
                        <el-tag type="danger" v-if="scope.row.pay_status === '0' "></el-tag>
                        <el-tag type=" success" v-else></el-tag>

                    </template>

                </el-table-column>
                <el-table-column label="" prop="is_send"></el-table-column>
                <el-table-column label="" prop="update_time">
                    <template slot-scope="scope">
                        {{scope.row.update_time | dateFormat}}
                    </template>

                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox"></el-button>
                        <el-button type="success" icon="el-icon-location" size="mini" @click="showProgressBox">
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>



            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum" :page-sizes="[10,15,20]" :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>

        </el-card>



        <el-dialog title="" :visible.sync="addressVisible" width="50%" @close="addressClose">
            <el-form :model="addressForm" :rules="addressRoules" ref="addressRef" label-width="100px">
                <el-form-item label="/" prop="address1">
                    <!-- <el-input v-model="addressForm.address1"></el-input> -->
                    <el-cascader :options="cityData" v-model="addressForm.address1" :props="addressProps"></el-cascader>
                </el-form-item>
                <el-form-item label="" prop="address2">
                    <el-input v-model="addressForm.address2"></el-input>
                </el-form-item>

            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addressVisible = false"> </el-button>
                <el-button type="primary" @click="dialogVisible = false"> </el-button>
            </span>
        </el-dialog>




        <el-dialog title="" :visible.sync="progressVisible" width="50%">

            <span slot="footer">
                <el-button @click="progressVisible = false"> </el-button>
                <el-button type="primary" @click="progressVisible = false"> </el-button>
            </span>
        </el-dialog>



    </div>
</template>

<script>
    import cityData from './cityData.js'
    export default {
        name: 'Order',
        data() {
            return {
                queryInfo: {
                    query: '',
                    pagenum: 1,
                    pagesize: 10
                },
                total: 0,
                orderList: [],
                addressVisible: false,
                addressForm: {},
                addressRoules: {
                    address1: [{ required: true, message: '', trigger: 'blur' }],
                    address2: [{ required: true, message: '', trigger: 'blur' }],
                },
                addressForm: {
                    address1: [],
                    address2: ''
                },
                cityData,
                addressProps: {
                    expandTrigger: 'hover',
                    children: 'children'
                },
                progressVisible: false
            }
        },
        created() {
            this.getOrderList()
        },
        methods: {
            async getOrderList() {
                const { data: res } = await this.$http.get('orders', {
                    params: this.queryInfo
                })
                if (res.meta.status !== 200) {
                    return this.$message.error('')
                }
                // console.log(res);
                this.orderList = res.data.goods
                this.total = res.data.total
            },
            handleSizeChange(newSize) {
                this.queryInfo.pagesize = newSize
                this.getOrderList()
            },
            handleCurrentChange(newPage) {
                this.queryInfo.pagenum = newPage
                this.getOrderList()
            },
            // 
            showBox() {
                this.addressVisible = true
            },
            addressClose() {
                this.$refs.addressRef.resetFields()
            },
            async showProgressBox() {

                this.progressVisible = true

            }
        }
    }
</script>

<style scoped>
    .el-cascader {
        width: 100%;
    }
</style>